.mainBlock {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-image: url('../../assets/125.jpg');
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: cover;
  a {
    display: inline-block;
    margin-top: 20px;
    text-decoration: none;
    color: black;
    line-height: 30px;
  }
  .text {
    position: relative;
    font-size: 100px;
    line-height: 100px;
    color: #ffffff;
    font-weight: 600;
    letter-spacing: -0.045em;
    text-align: center !important;
    @media screen and (max-width: 1080px) {
      margin-left: 20px;
      margin-right: 20px;
      font-size: 86px;
      line-height: 86px;
    }
    @media screen and (max-width: 720px) {
      margin-left: 5px;
      margin-right: 5px;
      font-size: 60px;
      line-height: 60px;
    }
  }
}

.photo {
  width: 300px;
  height: 400px;
  background-color: darkgrey;
}
