.nav {
    width: 360px;
    display: flex;
    justify-content: space-evenly;
    a{
        cursor: pointer;
        position: relative;
        font-weight: 600;
        display: inline-block;
        text-decoration: none;
        -webkit-transition: all .3s linear;
        -moz-transition: all .3s linear;
        transition: all .3s linear;
    }
    a:hover{
        color: #222;
    }

    a:before, a:after{
        content: '';
        background: #e74c3c;
        position: absolute;
        width: 0;
        height: 2px;
        -webkit-transition: all .3s linear;
        -moz-transition: all .3s linear;
        transition: all .3s linear;
    }

    a:before{
        top: 0;
        left: 0;
    }

    a:after{
        top: 100%;
        right: 0;
    }

    a:hover:before, a:hover:after{
        width: 100%;
    }
}

